.tasks {
    list-style: none;
    margin: 0;
    padding: 10px;
    font-size: large;
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    grid-gap: 10px;
    border-bottom: 1px solid rgb(245, 235, 235);
    padding: 10px;
    padding-left: 0;
}

.tasks__item--hidden {
    display: none;
}

.tasks__content {
    text-decoration: none;
}

.tasks__content--done {
    text-decoration: line-through;
}

.tasks__button {
    color: white;
    border-style: none;
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.tasks__button--toggleDone {
    color: white;
    background: green;
}

.tasks__button--toggleDone:hover {
    cursor: pointer;
    background: rgb(37, 167, 37);
}

.tasks__button--remove {
    background-color: firebrick;
    color: white;
}

.tasks__button--remove:hover {
    cursor: pointer;
    background-color: rgb(222, 69, 69);
}